export default {
  name: 'banner',
  props: {
    swiperList: Array,
    borderWidth: {
      type: String,
      default: 'padding: 1px'
    }
  },
  data() {
    return {
      indicatorDots: false,
      autoplay: true,
      interval: 5000,
      duration: 1000,
      indicatorActiveColor: '#fff'
    }
  },
  methods: {
    // 轮播图跳转
    naviget(link) {
      // console.log(link)
      let goods = link.indexOf('goods_id=')
      let article = link.indexOf('article_id=')
      let luckydraw = link.indexOf('luckydraw_id=')
      if(goods !== -1) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + link.slice(9)
          // url: 'goodDetail' 
        })
        return
      }
      if(article !== -1) {
        uni.navigateTo({
          url: '../components/shareInfo?article_id=' + link.slice(11)
        })
        return
      }
      if(luckydraw !== -1) {
        uni.navigateTo({
          url: '../zerodraw/zerodraw?luckydraw_id=' + link.slice(13)
        })
        return
      }
    }
  }
}
